import logger from"@accedo/xdk-log";import{util}from"@accedo/xdk-core";const{logError:logError}=logger({logTag:"[samsung-tizen/api/validator]"});export const INVALID="__XDK:validator:invalid";export const validate=(r,o)=>{if(util.isUndefined(r)||!util.isString(r))return logError("Error with Tizen API, apiName is undefined or invalid format."),INVALID;try{let t=r.split(".");switch(t.length){case 1:return window[t[0]](o);case 2:return window[t[0]][t[1]](o);case 3:return window[t[0]][t[1]][t[2]](o);case 4:return window[t[0]][t[1]][t[2]][t[3]](o);default:return logError(`Error with API: ${r}, unsupported api method length.`),INVALID}}catch(o){return logError(`Error with API: ${r}, ${o.message}`),INVALID}};